@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
}

.Splash-page {
  background: linear-gradient(to bottom right,#38C57F,#1FBDA9);
  color: #030D2E;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-family: 'Barlow';
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.button {
  background-color: #030D2E !important;
  color:#F5F4F6 !important;
  border: none !important;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.3s;

}
.button:hover, .button:focus, .button:active {
  background-color: #A8E541 !important;
  color:#030D2E !important;
  border: none !important;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: none;

}
#description { 
    margin-top: 0;
    font-family: 'Barlow';
    font-weight: 300;
    font-style: italic;
}

#title {
    margin-bottom: 1rem;
}

